.gpt3__header{
    display: flex;
}
.gpt3__header-content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    margin-right: 5rem;
}
.gpt3__header-content h1{
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    /* or 121% */

    letter-spacing: -0.04em;
}
.gpt3__header-content p {
    font-family: var(--font-family);
    font-style: normal;
    font-weight:  normal;
    font-size: 20px;
    line-height: 27px;

    color:var(--color-text); 
    margin-top: 1.5rem;
}
.gpt3__header-content__input {
    width: 100%;
    margin: 2rem 0 1rem ;

    display: flex;
}
.gpt3__header-content__input input {
    flex: 2;
    width: 100%;
    min-height: 50px;
    background: var(--color-footer);
    border: 2px solid var(--color-footer);
    font-family: var(--font-family);
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 27px;
    color:var(--color-text);
    padding: 0 1rem;
    color: white;
    outline: none;
    border-top-right-radius: 5px;
    border-bottom-left-radius: 5px;
}
.gpt3__header-content__input button {
    flex: 0.6;
    width: 100%;
    min-height: 50px;

    background: #FF4820;
    font-family: var(--font-family);
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 27px;
    cursor: pointer;
    color: #FFFFFF;
    outline: none;
    padding: 0 1rem;
    border: none;
    border-top-right-radius: 5px;
    border-bottom-left-radius: 5px;
}
.gpt3__header-content__people {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;

    margin-top: 2rem;
}
.gpt3__header-content__people img{
    width: 181.79px;
    height: 38px;
}
.gpt3__header-content__people p{
    margin-left: 1rem;
    font-family: var(--font-family);
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 27px;
    /* identical to box height, or 313% */
    text-align: center;
    margin-top: 0;
    color: #FFFFFF;
}
.gpt3__header-image {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}
.gpt3__header-image img {
    width: 100%;
    height: 100%;
}
@media screen and (max-width : 1050px ){
    .gpt3__header {
        flex-direction: column;
    }
    .gpt3__header-content {
        margin: 0 0 3rem;
    }
}
@media screen and (max-width : 650px ){
    .gpt3__header h1{
        font-size: 48px;
        line-height: 60px;
    }
    .gpt3__header p {
        font-size: 15px;
        line-height: 20px;
    }
    .gpt3__header-content__input input , .gpt3__header-content__input button{
        flex: 0.8;
        font-size: 16px;
        line-height: 20px;
    }
    .gpt3__header-content__people {
        flex-direction: column;
    }
    .gpt3__header-content__people p {
        margin: 0;
    }
}
@media screen and (max-width: 550px ) {
    .gpt3__header h1{
        font-size: 38px;
        line-height: 45px;
    }
    .gpt3__header p {
        font-size: 12px;
        line-height: 18px;
    }
    .gpt3__header-content__input input , .gpt3__header-content__input button{
        flex: 0.8 1;
        font-size: 14px;
    }
    .gpt3__header-content__people {
        flex-direction: column;
    }
    .gpt3__header-content__people p {
        margin: 0;
    }
    
}
