.gpt3__possibility {
    display: flex;
    flex-direction: row;
}
.gpt3__possibility-image {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;

    margin-right: 2rem;
}
.gpt3__possibility-image img {
    width: 100%;
    height: 100%;
}
.gpt3__possibility-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
}
.gpt3__possibility-content h4 {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    margin-bottom: 1rem;
    /* identical to box height, or 188% */


    color: #71E5FF;
}

.gpt3__possibility-content h4:last-child {
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    /* identical to box height, or 188% */


    color: #FF8A71;
}
.gpt3__possibility-content h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
    margin-bottom: 1rem;
}
.gpt3__possibility-content p {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 30px;
    margin-bottom: 2rem;
    /* or 188% */
    color: #81AFDD;
}
@media screen and ( max-width:950px) {
    .gpt3__possibility {
        flex-direction: column;
    }
    .gpt3__possibility h4 , .gpt3__possibility h4:last-child {
        margin-top: 2rem;
    }
    .gpt3__possibility p {
        margin-bottom: 0rem;
    }
}
@media screen and (max-width:400px) {
    .gpt3__possibility-image {
        margin-right: 0;
    }    
}